.detail_for_center {
    width: 100%;
    align-items: center;
    justify-content: center;
}



.detail_inner {
    padding: 20px 20px 40px 20px;
    display: flex;
    flex-direction: column;
    justify-content: center;
}


.detail_img {
    width: 520px;
    height: 240px;
    display: flex;
    flex-direction: column;
    justify-content: center;
}

.detail_name {
    padding-top: 16px;
    width: 100%;
    align-items: center;
    justify-content: center;
    text-align: center;
}

.composition {
    display: flex;
    gap: 20px;
    color: #8585AD;
    padding-top: 32px;
    width: 100%;
    align-items: center;
    justify-content: center;
    text-align: center;
}

.composition_inner {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}